import { FC } from 'react';
import { DataGrid, GridRowsProp, GridColDef } from "@mui/x-data-grid";

const columns: GridColDef[] = [
    { field: "id", hide: true },
    { field: "device", headerName: "Equipamento", width: 150 },
    { field: "amount", headerName: "Quantidade", width: 150 },
    { field: "time", headerName: "Tempo", width: 150 },
    { field: "watts", headerName: "Potência", width: 150 },
  ];

  const rowss: GridRowsProp = [
    { id: 1, device: "TV", watts: 100 },
    { id: 2, device: "Rádio", watts: 50 },
    { id: 3, device: "PC", watts: 400 },
    { id: 4, device: "Aspirador de pó", watts: 1200 },
  ];

const GridDevices:FC<{rows?:GridRowsProp}> = ({rows = []}) => {
  return (
    <DataGrid rows={rows} columns={columns} />
  )
}

export default GridDevices